.overlay{
  background: #FFFFFF;
  mix-blend-mode: normal;
  opacity: 0.96;
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  display: none;
  overflow-x:hidden;
  z-index: 100;
}

.sidebar {
  width: 320px;
  display: none;
  flex-direction: column;
  height: auto;
  left: 0;
  top: 0;
  bottom: 0;
  background: #FFFFFF;
  box-shadow: -2px 0px 4px rgba(69, 79, 126, 0.02), 16px 0px 52px rgba(14, 24, 80, 0.2);
  padding-bottom: 32px;
}

.container{
  display: flex;
  align-items: center;
  flex-wrap: nowrap;
  /* сверху | справа | снизу | слева */
  padding: 24px 23px 0 23px;
}

.container-wide{
  justify-content: space-between;
}

.icon{
  width: 40px;
  height: 40px;
  background-color: #FF3E79;
  background-repeat: no-repeat;
  background-position: 50% 50%;
  border-radius: 20px;
}

.icon-close{background-image: url("/img/block1/icon/icon-close.png");}

.icon-search{background-image: url("/img/block1/icon/icon-search.png");}

.icon-phone {background-image: url("/img/block1/icon/icon-phone.png");}

.icon-chat {background-image: url("/img/block1/icon/icon-chat.png");}

.icon-contacts {background-image: url("/img/block1/icon/icon-contacts.png");}


@keyframes rotate {
  50% {
    transform: rotate(45deg);
  }
}

.icon:hover {
  animation-name: rotate;
  animation-duration: 2s;
  cursor: pointer;
  filter: opacity(0.3);
  transition: 2s;
}

/*--------------navigation--------------------*/

.list{
  list-style: none;
}

.item{
  user-select: none;
}

.item > input[type="radio"] {
  display: none;
}

.item > label:not(:first-child){
  margin-top: 24px;
}

.item > label {
  display: flex;
  align-items: center;
  cursor: pointer;
  color: #1B1C21;
  letter-spacing: -0.2px;
}

.item > label::after {
  content: "";
  position: absolute;
  left: 0;
  height: 32px;
  color: #7E7E82;
}

.item > input:checked + label::after {
  border: 4px solid #41F6D7;
  border-radius: 4px;
  color: #FF3E79;
}

.overlay-active{
  display: block;
}

.active-menu{
  display: block;
  height: 100vh;
  position: fixed;
  z-index: 100;
  transition-duration: .2s;
}


/*-------------------contacts---------------------*/
/*.container:last-of-type{*/
/*  padding-bottom: 16px;*/
/*}*/
.contacts-list .icon:not(:first-child){
  margin-left: 16px;
}

a {
  text-decoration: none;
  color: inherit;
}

.phone a{
  font-family: 'TT Lakes Bold';
  font-size: 24px;
  line-height: 32px;
  letter-spacing: -0.6px;

}

/*--------------language----------------------*/
.contacts{
  margin-top: auto;
}

.list-item > input[type="radio"] {
  display: none;
}

.language-list{
  display: flex;
}

.language-list .list-item > label::after {
  content: "";
  position: absolute;
  left: 20px;
  color: #7E7E82;
}

.language-list .list-item label{
  cursor: pointer;
}

.language-list .list-item:not(:first-child){
  margin-left: 16px;
}

.language-list .list-item > input:checked + label {
  border-radius: 4px;
  color: #1B1C21;
  font-weight: bold;
}

@media screen and (min-width: 320px){
  .container{
    padding:10px 20px 0 18px
  }
  .item > label:not(:first-child){
    margin-top: 15px;
  }
}
@media screen and (min-width: 768px){
  .contacts{
    margin-top: 150px;
  }
}

@media screen and (min-width: 1140px){
  .sidebar .icon-close{
    visibility: hidden;
  }
}


/*-------------------------------fonts------------------------------*/

